.SceneHolder{
    background-image:linear-gradient(141deg, #9fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
    width:100%;
    height:100vh;
    position: relative;
    overflow-x:hidden;
}

#bigMountain{
    position: absolute;
    z-index:1;
    height:35rem;
    bottom:0;
    left:5%;
}

#mediumMountain{
    position: absolute;
    z-index: 1;
    height: 25rem;
    bottom:0;
    right:5%;
}

#tallTree1{
    position: absolute;
    z-index:2;
    height:18rem;
    bottom:0;
    left:0;
    right:0;
}

#mediumTree1{
    position: absolute;
    z-index:2;
    height: 12rem;
    bottom:0;
    right:0;    
}

#tallTree2{  
    position: absolute;
    z-index:3;
    height: 18rem;
    bottom:0;
    right:40%;  
}

#mediumTree2{
    position: absolute;
    z-index:2;
    height: 12rem;
    bottom:0;
    right:50%;  
}
#mediumTree3{
    position: absolute;
    z-index:2;
    height: 12rem;
    bottom:0;
    right:35%;  
}

@media screen and (max-width:60rem){
    #mediumTree1{
        position: absolute;
        z-index:2;
        height: 12rem;
        bottom:0;
        right:15%;    
    }
    
    #tallTree2{  
        position: absolute;
        z-index:3;
        height: 18rem;
        bottom:0;
        right:0;  
    }
    
    #mediumTree2{
        position: absolute;
        z-index:2;
        height: 12rem;
        bottom:0;
        left:15%;  
    }
    #mediumTree3{
        position: absolute;
        z-index:2;
        height: 10rem;
        bottom:0;
        right:25%;  
    }
}

@media screen and (max-height:768px){
    #bigMountain{
        position: absolute;
        z-index:1;
        height:45rem;
        bottom:0;
        left:5%;
    }
    
    #mediumMountain{
        position: absolute;
        z-index: 1;
        height: 32rem;
        bottom:0;
        right:5%;
    }
    
    #tallTree1{
        position: absolute;
        z-index:2;
        height:22rem;
        bottom:0;
        left:0;
        right:0;
    }
    
    #mediumTree1{
        position: absolute;
        z-index:2;
        height: 15rem;
        bottom:0;
        right:0;    
    }
    
    #tallTree2{  
        position: absolute;
        z-index:3;
        height: 22rem;
        bottom:0;
        right:40%;  
    }
    
    #mediumTree2{
        position: absolute;
        z-index:2;
        height: 15rem;
        bottom:0;
        right:50%;  
    }
    #mediumTree3{
        position: absolute;
        z-index:2;
        height: 15rem;
        bottom:0;
        right:35%;  
    }
}